@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
body {
    font-family: "DM Sans",sans-serif;
    background: #fbfeff;
}

/* #betmount {
    max-width: 100%;
    overflow-x: hidden;
} */
.tradeList {
    width: auto;
    margin: 24px 16px;
    border-radius: 20px;
}
  
.site-layout .site-layout-background {
    background: #fbfeff;
}

.ant-card-hoverable {
    cursor: default;
}

.howToTrade .ant-drawer-content-wrapper {
    height: 100vh !important;
}
::-webkit-scrollbar {
    height: 0;
}

/* .categoryListTest {
    width: calc(100% - 34px);
    margin: 18px auto 0;
    border-radius: 20px;
} */

.categoryList {
    display: grid;
    width: auto;
    margin: 24px 16px;
    border-radius: 20px;
}

.categoryList .ant-card-body {
    display: flex;
    overflow-x: auto;
}

.categoryList .ant-card-meta-avatar {
    padding-right: 0;
}

.category {
    text-align: center;
    display: flex;
    flex-direction: column;
    margin: 0 10px;
    cursor: pointer;
}

.categoryIcon {
    margin-bottom: 8px;
}

.category:hover .categoryIcon {
    border-color: transparent;
    box-shadow: 0 1px 2px -2px rgb(0 0 0 / 16%), 0 3px 6px 0 rgb(0 0 0 / 12%), 0 5px 12px 4px rgb(0 0 0 / 9%);
    transition: box-shadow 0.3s, border-color 0.3s;
}

/* .ant-card.ant-card-bordered.ant-card-contain-grid .ant-card-body {
    display: -webkit-inline-box;
    overflow-x: scroll;
} */

.ant-card-grid.ant-card-grid-hoverable {
    padding: 10px;
}

.ant-card-meta-title {
    white-space: normal;
}
.primary-btn {
    color: #1da1f2;
    background: linear-gradient(0deg,#e9f7fe,#e9f7fe);
    border-color: #1da1f2;
}

.ant-btn-dangerous {
    color: #f93d4b;
    background: linear-gradient(0deg,#fbf0f3,#fbf0f3);
    border-color: #f93d4b;
}

.tradeActions {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 15px 0 0;
}

.tradeAction {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.tradeFill {
    font-weight: bold;
    background: linear-gradient(0deg,#e9f7fe,#e9f7fe);
    display: flex;
    align-items: center;
    border-radius: 20px;
    padding-left: 10px;
    margin-right: 10px;
}

.tradeDangerFill {
    background: linear-gradient(0deg,#fbf0f3,#fbf0f3);
}

.ant-avatar {
    width: 60px;
    height: 60px;
}

.tradeDrawer .ant-drawer-content-wrapper {
    height: 550px !important;
}

.tradeDrawer.yesDrawerBackground .ant-drawer-body {
    background: linear-gradient(0deg,#e9f7fe,#e9f7fe);
}

.tradeDrawer.noDrawerBackground .ant-drawer-body {
    background: linear-gradient(0deg,#fbf0f3,#fbf0f3);
}

.ant-drawer-content {
    border-radius: 20px 20px 0 0;
}

.tradeSubmissionDrawer {
    font-size: 20px;
    text-align: center;
}

.tradeInput {
    background: white;
    width: 90%;
    margin: 15px auto 10px;
    padding: 30px 0;
    border-radius: 20px;
    box-shadow: 0 1px 2px -2px rgb(0 0 0 / 16%), 0 3px 6px 0 rgb(0 0 0 / 12%), 0 5px 12px 4px rgb(0 0 0 / 9%);
}

.sliderText {
    position: absolute;
    top: -14px;
    font-size: 16px;
    line-height: 1;
    left: 0;
}

.priceText {
    top: 30px;
}

.ant-input-number {
    width: 60px;
}

.tradeDrawerFooter {
    background: #eee;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    left: 0px;
    right: 0;
}

@media only screen and (max-width: 575px) {
    .ant-card-meta {
        display: flex;
        flex-direction: column;
    }
    
    .ant-card-meta-avatar {
        justify-content: center;
        display: flex;
        margin-bottom: 10px;
    }

    .tradeActions {
        justify-content: space-between;
        flex-direction: row-reverse;
    }

    .tradeFill {
        display: none;
    }
}
.tradeResults {
    font-size: 18px;
    margin-bottom: 0;
    padding-top: 15px;
}
.tradeResults {
    font-size: 18px;
    margin-bottom: 0;
    padding-top: 15px;
}
.ant-layout-sider-children {
    display: flex;
}

.ant-layout-sider-children .ant-menu.ant-menu-inline-collapsed {
    width: 80px;
}

.ant-layout-sider-trigger {
    width: 170px !important;
}

.ant-layout-sider-has-trigger {
    display: contents;
}

.ant-layout-sider-collapsed .ant-layout-sider-trigger {
    width: 79px !important;
}

.ant-menu-inline {
    width: auto;
}

.logo-wrapper {
    display: flex;
    justify-content: center;
    margin: 15px;
}
  
.logo {
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 0.5px;
    color: white;
    display: flex;
    align-items: center;
    margin-left: 10px;
    
}

.downloadAppBtn {
    display: block;
    margin: 10px auto;
}

@media only screen and (max-width: 320px) {
    .downloadAppBtn {
        display: none;
    }
}
